body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Bylynn Sans,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  margin: 10px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}
* {
  box-sizing: border-box;
}
body {
  background-color: #eee;
  text-align: center;
}
.Row {
  align-items: center;
  display: flex;
  justify-content: center;
}
.Row-letter,
.Row-letter-color {
  align-items: center;
  border: 2px solid #8ed5ff;
  display: flex;
  flex: 1 1;
  font-size: 28px;
  font-weight: 700;
  height: 40px;
  justify-content: center;
  margin: 2px;
  max-width: 40px;
  text-transform: uppercase;
}
.Row-annotation {
  -webkit-margin-start: 16px;
  margin-inline-start: 16px;
  text-align: start;
  width: 5em;
}
.App-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  max-width: 500px;
  position: relative;
}
.App-container h1 {
  margin-top: 0;
}
.Game,
h1 {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.Game,
table.Game-rows {
  display: flex;
  flex-direction: column;
}
table.Game-rows {
  margin: auto;
}
table.Game-rows:focus {
  outline: none;
}
.Game-keyboard,
table.Game-rows > tbody {
  display: flex;
  flex-direction: column;
}
.Game-keyboard-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
}
.Game-keyboard-button {
  align-items: center;
  background-color: white;
  border: 2px solid transparent;
  border-radius: 4px;
  color: inherit;
  cursor: pointer;
  display: flex;
  flex: 1 1;
  font-family: inherit;
  font-size: 20px;
  justify-content: center;
  margin: 2px;
  min-height: 40px;
  padding: 2px;
  text-decoration: inherit;
  text-transform: capitalize;
}
.Game-keyboard-button-wide {
  flex: 2 1;
}
.Game-keyboard-button:focus {
  outline: none;
}
.letter-correct {
  background-color: #57ac78;
  border: 2px solid rgba(0, 0, 0, 0.3);
  color: #fff !important;
}
.letter-elsewhere {
  background-color: #e9c601;
  border: 2px dotted rgba(0, 0, 0, 0.3);
  color: #fff !important;
}
.letter-absent {
  background-color: #597496;;
  border: 2px solid transparent;
  color: #fff !important;
}
body.dark {
  background-color: #202B38;
  color: #8ed5ff;
  color-scheme: dark;
}
body.dark .Game-keyboard-button {
  color: #404040;
}
a,
a:visited {
  color: #8ed5ff;
}
a:active {
  color: #8ed5ff;
}
.Game-options {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}
.Game-options > * + * {
  -webkit-margin-start: 0.5rem;
  margin-inline-start: 0.5rem;
}
.Game-options button {
  min-width: 4rem;
}
.App-footer {
  font-size: 80%;
  line-height: 1.5;
  margin: -1rem 0 2rem;
}
.App-about {
  line-height: 1.4;
  margin-top: -1rem;
}
.App-about b {
  background-color: #888;
  border-radius: 2px;
  color: #fff;
  padding: 1px 3px;
}
.App-about b.green-bg {
  background-color: #57ac78;
}
.App-about b.yellow-bg {
  background-color: #e9c601;
}
.Game-seed-info {
  -webkit-font-feature-settings: "tnum";
  font-feature-settings: "tnum";
  font-variant-numeric: tabular-nums;
  margin-top: 1em;
  opacity: 0.7;
}
.Game-sr-feedback,
.sr-only {
  height: 1px;
  left: -10000px;
  overflow: hidden;
  position: absolute;
  top: auto;
  width: 1px;
}
.Settings {
  font-size: 18px;
  text-align: left;
}
.Settings-setting {
  align-items: top;
  display: flex;
  margin: 8px;
}
.Settings-setting input {
  height: 18px;
  width: 18px;
}
.Settings-setting input[type="range"] {
  height: 18px;
  width: 50px;
}
.Settings-setting label {
  -webkit-margin-start: 8px;
  -webkit-margin-end: 8px;
  margin-inline-end: 8px;
  margin-inline-start: 8px;
}
.top-right {
  position: absolute;
  right: 5px;
  top: 5px;
}
.emoji-link {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 24px;
  padding: 0;
  text-decoration: none;
}
.top-right > * + * {
  -webkit-margin-start: 8px;
  margin-inline-start: 8px;
}
.App-container.color-blind .App-about b.green-bg,
.App-container.color-blind .letter-correct {
  background-color: #f5793a;
}
.App-container.color-blind .App-about b.yellow-bg,
.App-container.color-blind .letter-elsewhere {
  background-color: #85c0f9;
}
